$color-primary: #dbe3ea
$color-secondary: #40526c
$color-secondary-shadow: #203249
//$color-secondary: #8293af
//$color-secondary-shadow: #dce3ed

$height-header: 10vw
$default-margin: 1vw
$default-border: 0.4vw

.background-animated
  position: absolute
  width: 100%
  height: 100%
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  overflow: hidden

  .animation-item
    width: 120%
    height: 90%
    background: linear-gradient(rgba(211, 134, 134, 0.33), rgba(5, 166, 181, 0.33))
    border-radius: 30% 80% 80% 30% / 30% 30% 80% 80%
    filter: blur(50px)
    animation: rotate 40s infinite linear, shape 15s infinite linear, size 25s infinite linear
    -webkit-animation: rotate 40s infinite linear, shape 15s infinite linear, size 25s infinite linear
    -o-animation: rotate 40s infinite linear, shape 15s infinite linear, size 25s infinite linear
    @keyframes rotate
      0%
        transform: rotate(0deg)
      100%
        transform: rotate(360deg)
    @keyframes shape
      0%
        border-radius: 30% 80% 80% 30% / 30% 30% 80% 80%
      25%
        border-radius: 80% 30% 80% 30% / 30% 30% 80% 80%
      50%
        border-radius: 80% 80% 30% 30% / 80% 10% 30% 10%
      75%
        border-radius: 10% 10% 70% 70% / 50% 20% 80% 90%
      100%
        border-radius: 30% 80% 80% 30% / 30% 30% 80% 80%
    @keyframes size
      0%
        width: 120%
        height: 90%
      50%
        margin-left: 5%
        width: 90%
        height: 120%
      100%
        width: 120%
        height: 90%

.WruPort
  display: flex
  flex-flow: column nowrap
  background-color: $color-secondary
  color: $color-primary
  width: 100vw
  height: 100vh
  overflow: hidden

  .header
    width: 100%
    display: flex
    flex-flow: row nowrap
    font-size: 2vw
    position: absolute
    backdrop-filter: blur(200px)
    -webkit-backdrop-filter: blur(80px)
    .avatar
      width: calc($height-header - (2*$default-margin) - (2*$default-border))
      height: calc($height-header - (2*$default-margin) - (2*$default-border))
      border-radius: 50%

      border: $default-border solid $color-secondary-shadow
      background-color: #42608a
      margin: $default-margin
    .menu
      display: flex
      flex-flow: row nowrap
      flex: 1
      justify-content: flex-end
      .menu-item
        line-height: calc($height-header - 4vw)
        //background-color: $color-secondary-shadow
        padding: 2vw
        //border-right: 1px solid $color-secondary
        background: linear-gradient($color-secondary-shadow 0 0) left 100% / var(--p, 0%) 10% no-repeat
        //background-size: 2px 100%
        transition: 0.4s
        cursor: pointer
      .menu-item:last-of-type
        border-right: none
      .menu-item:hover
        --p: 100%
